.main {
      width: 100%;
      height: 100%;
      background-image: url("../assets/image/space1.jpeg");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: 20% bottom;
}

.left {
      max-height: 330px;
      max-width: 600px;
      /* float: right; */
      /* text-align: right; */
      position: absolute;
      margin-right: 20px;
      margin-top: 60px;
}

.heading {
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-weight: 800;
      font-size: 140px;
      line-height: 180px;
      letter-spacing: -0.02em;
      color: #FFFFFF;
      text-shadow: -7px 7px 10px rgba(255, 255, 255, 0.32);
      transition: all .2s;
}

.p {
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 30px;
      line-height: 40px;
      letter-spacing: -0.02em;
      color: #FFFFFF;
      text-shadow: -7px 7px 10px rgba(255, 255, 255, 0.32);
      transition: all .2s;
}

.goback {
      font-size: 20px;
      text-shadow: -7px 7px 10px rgb(52, 152, 219, 0.32);
      font-weight: 900;
      color: #3498db;
      text-decoration: none;
}

.goback:hover {
      text-decoration: underline;
}

@keyframes ship {
      from, to {
            transform: rotateX(180deg) translateX(0) translateY(0);
      }
      50% {
            transform: rotateX(180deg) translateX(-15px) translateY(30px);
      }
}

@media screen and (max-width: 1000px) {
      .left {
            margin-top: 0;
      }
}

@media screen and (max-width: 630px) {
      .heading {
            font-size: 100px;
            line-height: 120px;
      }
      .p {
            font-size: 30px;
            line-height: 40px;
      }
}

@media screen and (max-width: 360px) {
      .heading {
            font-size: 80px;
            line-height: 110px;
      }
      .p {
            font-size: 20px;
            line-height: 30px;
      }
}