.main {
      position: absolute;
      min-height: 120px;
      min-width: 120px;
      background-color: #222;
      overflow: hidden;
      border-radius: 5px;
      animation: popup .90s ease-in-out forwards;
}

@keyframes popup {
      from {
            transform: scale(0);
      }
      to {
            transform: scale(1);
      }
}

.header {
      width: 100%;
      height: 30px;
      background: #000;
      cursor: move;
      border-bottom: 1px solid #6a6a6a;
}

.title {
      line-height: 30px;
      font-size: 15px;
      padding: 0 0 0 10px;
      display: inline-block;
      vertical-align: top;
}

.close {
      float: right;
      height: 100%;
      width: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      vertical-align: top;
      cursor: pointer;
      font-size: 15px;
}

.close:hover {
      background-color: #f00;
}

.body {
      position: relative;
      width: 100%;
      height: calc(100% - 30px);
      overflow: hidden;
}