.main {
      width: 100%;
      height: 100%;
      /* padding: 5px; */
      background-color: #202020;
}

.header {
      width: 100%;
      height: 80px;
      background-color: #191919;
}

.head {
      width: 100%;
      height: 40px;
}

.head h2 {
      height: 100%;
      line-height: 40px;
      margin-left: 10px;
}

.toolbar {
      width: 100%;
      height: 40px;
      padding: 7px;
}

.tool {
      height: 100%;
      aspect-ratio: 1;
      /* background-color: #fff; */
      border: 1px solid rgb(48, 48, 48);
      border-radius: 5px;
      cursor: pointer;
      margin: 0 3px;
}

.tool:hover {
      border: 1px solid rgb(94, 94, 94);
}

.folderTreeBar {
      display: inline-block;
      height: 100%;
      width: calc(100% - 90px);
      vertical-align: top;
}

.folderTreeBar .tree {
      border: 1px solid rgb(48, 48, 48);
      border-radius: 5px;
      display: inline-block;
      width: 60%;
      height: 100%;
      vertical-align: top;
      margin-right: 5px;
      white-space: nowrap;
}

.folderTreeBar .tree .treeItem {
      display: inline-block;
      margin: 1.6px 6px;
      background-color: #333;
      padding: 1px 3px;
      border-radius: 3px;
      border: none;
      font-size: 13.3px;
      line-height: 18px;
}

.folderTreeBar .tree .treeItem::after {
      content: "";
      background-image: url("../../assets/svg/expand_white.svg");
      background-size: cover;
      background-repeat: no-repeat;
      width: 18px;
      height: 18px;
      position: absolute;
}

.folderTreeBar .searchBar {
      display: inline-block;
      width: calc(40% - 5px);
      height: 100%;
      border: 1px solid rgb(48, 48, 48);
      border-radius: 5px;
      vertical-align: top;
}

.folderTreeBar .searchBar svg {
      width: 20px;
      height: 20px;
      margin: 2px;
}

.folderTreeBar .searchBar input {
      outline: none;
      border: none;
      height: 100%;
      width: calc(100% - 26px);
      background-color: transparent;
      color: #fff;
      vertical-align: top;
      margin-left: 2px;
}

.folderTreeBar .tree:hover, .folderTreeBar .searchBar:hover {
      border: 1px solid #5e5e5e;
}

.leftPanel {
      height: calc(100% - 80px);
      background-color: #191919;
      position: relative;
      max-width: 500px;
      min-width: 50px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
}

.leftPanelItemsHolder {
      height: 100%;
      width: 100%;
      padding: 5px;
      overflow: auto;
}

.leftPanelItemParent {
      width: 100%;
      margin: 2px 0;
}

.leftPanelItem {
      padding-left: 12px;
      border-left: 1px solid rgba(153, 153, 153, 0);
}

.leftPanelItem:hover {
      border-left: 1px solid #99999980;
}

.leftPanelItem p {
      font-size: 12px;
      font-weight: 0;
}

.leftPanelItemDir {
      cursor: pointer;
}

.leftPanelItemDir:hover {
      background-color: #ffffff1c;
}

.leftPanelItemDir h5 {
      display: inline-block;
      vertical-align: middle;
}

.leftPanelItemDir svg {
      vertical-align: middle;
      height: 20px;
      width: 20px;
}

.leftPanelItemFile {
      padding-left: 20px;
      cursor: pointer;
}

.leftPanelItemFile:hover {
      background-color: #ffffff1c;
}

.leftPanelItemFile svg {
      height: 20px;
      width: 20px;
      vertical-align: middle;
}

.leftPanelItemFile h5 {
      height: 20px;
      line-height: 15px;
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      display: inline-block;
      vertical-align: middle;
}

.loading {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 20px;
}

/**











* Main Content
*/

.content {
      height: calc(100% - 80px);
      display: inline-block;
      vertical-align: top;
      min-width: calc(100% - 500px);
      max-width: calc(100% - 50px);
      border-top: 1px solid #2b2b2b;
}

.contentItemHolder {
      display: inline-block;
      min-width: 100px;
      margin: 0;
      border: none;
      vertical-align: top;
      padding: 5px;
      justify-content: center;
}

.contentItem {
      width: 100px;
      cursor: pointer;
      padding: 5px;
      border-radius: 5px;
      overflow: hidden;
      position: relative;
}

.contentItem:hover {
      background-color: #ffffff1c;
}

.contentItem svg {
      height: 90px;
      width: 90px;
}

.contentItem p {
      font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      text-align: center;
      font-size: 12px;
      font-weight: 600;
      word-break: break-all;
}

.contextMenu {
      position: absolute;
      width: 170px;
      background-color: #000000da;
      border: 1px solid #6a6a6a;
      border-radius: 5px;
      overflow: hidden;
      opacity: 0;
      z-index: 100;
      animation: contextMenuFadeIn 0.2s ease-in-out forwards;
}

@keyframes contextMenuFadeIn {
      from {
            opacity: 0;
      }
      to {
            opacity: 1;
      }
}

.contextMenuIcons {
      height: 20px;
      vertical-align: middle;
      margin-right: 10px;
}

.contextMenuItem p {
      display: inline-block;
      vertical-align: middle;
      color: #fff;
}

.contextMenuItem {
      padding: 3px 10px;
      cursor: pointer;
      font-weight: 600;
      font-size: 12px;
      white-space: nowrap;
}

.contextMenuItem:hover {
      background-color: #ffffff1c;
}

.contextMenuItem:hover p {
      color: var(--hover-color);
}

.contextMenuItem:hover svg {
      fill: var(--hover-color);
}

.properties {
      position: absolute;
      /* width: 170px; */
      background-color: #000000da;
      border: 1px solid #6a6a6a;
      border-radius: 5px;
      overflow: hidden;
      opacity: 0;
      z-index: 100;
      padding: 5px;
      animation: contextMenuFadeIn 0.2s ease-in-out forwards;
}

.properties table {
      width: 100%;
      height: 100%;
      font-size: 13px;
      font-weight: 500;
      font-family: system-ui;
      white-space: nowrap;
}

.properties table tbody tr td {
      padding: 3px;
}

.newFileModal {
      max-width: 500px;
      width: 100%;
      min-width: 150px;
      margin: 10px;
      height: -webkit-fit-content;
      height: -moz-fit-content;
      height: fit-content;
      border-radius: 8px;
      background-color: #161b22;
      position: relative;
      box-shadow: 0 10px 15px 5px #000;
}

.Modals_heading {
      padding: 20px 10px;
      border-bottom: 1px solid #222;
}

.Modals_close {
      position: absolute;
      padding: 20px;
      top: 0;
      right: 0;
      cursor: pointer;
}

.Modals_body {
      padding: 10px;
}

.Modals_input {
      color: #111;
      width: 100%;
      padding: 5px 10px;
      margin-top: 5px;
      border: none;
      border-radius: 5px;
      outline: none;
      margin-bottom: 20px;
      box-shadow: 4px 4px 5px 3px #222;
}

.Modals_input * {
      color: #000;
}

.Modals_btn {
      padding: 8px 10px;
      border-radius: 8px;
      font-size: 13px;
      background-color: #222;
      display: inline-block;
      margin: 2px;
      cursor: pointer;
      border: none;
      outline: none;
}

.Modals_btn:hover {
      box-shadow: inset 0 0 10px 2px #000;
}

.response {
      width: 100%;
      text-align: center;
      background-color: #00ff0073;
      /* #ff00003d; */
      margin: 5px 0;
      border: 1px solid #00ff00aa;
      /* #ff00005e; */
      border-radius: 8px;
      padding: 6px;
      animation: responseBanner 0.25s linear;
      transition: all .75s;
      font-size: 12px;
}

@keyframes responseBanner {
      0% {
            opacity: 0;
      }
      100% {
            opacity: 1;
      }
}

.uploadDiv {
      height: 40vh;
      margin: 35px;
      border: 2px dashed #5c5c5c;
      border-radius: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      transition: all .25s ease-in-out;
      cursor: pointer;
}

.dragged.uploadDiv {
      background-color: #00000044;
      border: 2px dashed #bbb;
}

.uploadDiv * {
      pointer-events: none;
}

.uploadIconsContainer {
      width: 80px;
      height: 100px;
}

.uploadFileIcon {
      width: 80px;
      position: absolute;
      transform-origin: bottom;
      transition: all .25s ease-in-out;
}

.uploadFileIcon:nth-child(1) {
      z-index: 2;
}

.uploadTitle {
      font-size: 17px;
      font-weight: 600;
      color: #fff;
      text-align: center;
      margin-top: 10px;
}

.uploadTitle span {
      color: #47a6da;
}

.dragged .uploadFileIcon:nth-child(1) {
      transform: translateY(-5px);
}

.dragged .uploadFileIcon:nth-child(2) {
      transform: rotate(10deg) translateX(20px) scale(.9);
      filter: grayscale(.5);
}

.dragged .uploadFileIcon:nth-child(3) {
      transform: rotate(-10deg) translateX(-20px) scale(.9);
      filter: grayscale(.5);
}

.progress {
      margin: 35px;
}

.progressBarHolder {
      height: 35px;
      width: 100%;
      background-color: #00000070;
      border-radius: 5px;
      position: relative;
      overflow: hidden;
}

.progressBarHolder p {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      line-height: 35px;
      text-align: center;
      color: #fff;
      font-size: 15px;
      font-weight: 700;
}

.progressBar {
      position: absolute;
      height: 100%;
      background-color: #3d3d3d;
}

.uploadedFiles {
      margin-top: 10px;
      font: 15px sans-serif;
      font-weight: 700;
      text-align: center;
}