.main {
      width: 100%;
      height: 100%;
      background-image: url("../assets/image/bg-glow.svg");
      background-size: 130% 130%;
      background-repeat: no-repeat;
}

.window {
      width: 60%;
      aspect-ratio: 2/1;
      background-color: #0184dc;
      padding: 1px;
      border-radius: 5px;
      overflow: hidden;
      transition: all 1s;
      position: relative;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-50%);
}

@media screen and (max-width: 900px) {
      .window {
            width: 80%;
      }
}

@media screen and (max-width: 600px) {
      .main {
            padding: 10px;
      }
      .window {
            margin: 0;
            width: calc(100% - 20px);
            height: calc(100% - 20px);
      }
}

.cmdHead {
      width: 100%;
      height: 30px;
}

.cmdHead h3 {
      display: inline-block;
      line-height: 30px;
}

.cmdHead h3:nth-child(1) {
      margin-left: 10px;
      margin-right: 20px;
      color: #000;
}

.tool {
      /* display: inline-block; */
      float: right;
      width: 120px;
      height: 100%;
      /* background-color: #f00; */
}

.tool div {
      height: 100%;
      width: 33.33%;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      vertical-align: top;
      cursor: pointer;
}

.tool div:hover {
      background-color: #ffffff25;
}

.tool a div:hover {
      background-color: #f00;
}

.cmdBlock {
      width: 100%;
      height: calc(100% - 30px);
      background-color: #050505;
      /* background-image: url("../assets/image/bg-glow-single.svg");
      background-size: 130% 130%; */
      border-radius: 0 0 5px 5px;
      overflow-y: hidden;
      overflow-x: hidden;
      padding: 5px;
      font-family: monospace;
      font-size: 1em;
      line-height: 25px;
      cursor: text;
}

.welcomeText {
      color: #00cb00;
      line-height: 1em;
      font-size: 16px;
}

.cmdBlock a {
      /* color: #58a6ff; */
      color: #4487d3;
      text-decoration: none;
}

.cmdBlock a:hover {
      text-decoration: underline;
}

.CMDInput {
      position: relative;
}

.CMDInputSpan>span>span {
      margin-top: 5px;
      font-size: 15px;
      color: #00ffff;
}

.CMDInputSpan {
      height: 25px;
      width: 100%;
      font-size: 1em;
      font-family: monospace;
      color: #fff;
}

.CMDInputSpan>span {
      vertical-align: top;
      word-break: break-all;
}

.cursor {
      height: 1.25em;
      width: .5em;
      margin-top: 4px;
      display: inline-block;
      background-color: #fff;
      vertical-align: top;
}

.cursorBlink {
      animation: cursorBlink 1s step-end infinite;
}

@keyframes cursorBlink {
      from, to {
            background-color: transparent;
      }
      50% {
            background-color: #fff;
      }
}

.cmdInputTag {
      width: 0;
      height: 0;
      opacity: 0;
      position: absolute;
}