.main {
      width: 100%;
      height: 100%;
      background-image: url("../assets/image/bg-glow.svg");
      background-size: 130% 130%;
      background-repeat: no-repeat;
      display: flex;
      justify-content: center;
      align-items: center;
}

.glow_spot {
      top: 100%;
      left: 10%;
      position: absolute;
      width: 1px;
      height: 1px;
      border-radius: 50%;
      /*
            #934989 - pink,
            #04337a - blue,
            #137c8d - cyan,
      */
      box-shadow: 0px 0px 200px 200px #137c8d;
}

.Login {
      width: 100%;
      max-width: 300px;
      height: 300px;
      margin: 10px;
      background-color: #000000a6;
      border-radius: 10px;
      box-shadow: 0px 1px 20px 0px #1fc0c17a;
}

.head {
      width: 100%;
      padding: 10px;
      border-bottom: 1px solid #ffffff59;
}

.inputDiv {
      width: calc(100% - 40px);
      margin: 25px 20px;
      position: relative;
}

.input {
      font: 15px/24px "Lato", Arial, sans-serif;
      color: #ccc;
      width: 100%;
      box-sizing: border-box;
      letter-spacing: 1px;
      border: 0;
      padding: 4px 0;
      border-bottom: 1px solid #333;
      background-color: transparent;
      position: relative;
      z-index: 2;
}

.input:focus {
      outline: none;
}

.input~.focusBorder {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0;
      height: 2px;
      background-color: #3399FF;
      transition: 0.4s;
}

.input:focus~.focusBorder, .hasContent.input~.focusBorder {
      width: 100%;
      transition: 0.4s;
}

.input~label {
      position: absolute;
      left: 0;
      width: 100%;
      top: 9px;
      color: #aaa;
      transition: 0.3s;
      z-index: 1;
      letter-spacing: 0.5px;
}

.input:focus~label, .hasContent.input~label {
      top: -16px;
      font-size: 12px;
      color: #3399FF;
      transition: 0.3s;
}

.button {
      box-sizing: border-box;
      appearance: none;
      background-color: transparent;
      border: 2px solid #3498db;
      border-radius: 0.6em;
      color: #3498db;
      cursor: pointer;
      display: flex;
      align-self: center;
      font-size: .8rem;
      font-weight: 400;
      line-height: 1;
      margin: 20px;
      padding: .7em 1.4em;
      text-decoration: none;
      text-align: center;
      text-transform: uppercase;
      font-family: 'Montserrat', sans-serif;
      font-weight: 700;

      border-color: #3498db;
      color: #fff;
      box-shadow: 0 0 40px 40px #3498db inset, 0 0 0 0 #3498db;
      transition: all 150ms ease-in-out;
}

.button:hover, .button:focus {
      color: #fff;
      outline: 0;
      box-shadow: 0 0 10px 0 #3498db inset, 0 0 10px 4px #3498db;
}

.foot {
      padding: 10px;
      font: 13px "Lato", Arial, sans-serif;
}

.foot a {
      color: #3498db;
      text-decoration: none;
}

.foot a:hover {
      text-decoration: underline;
}

.errorHolder {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
}